.pagination{
    font-weight: bold;
}
.pagination-short{
    .page-item {
        margin-right: 6px;
        &:last-child{
            margin-right: 0;
        }
    }
}
.page-item{
    margin-right: 15px;
    @media (max-width: 575px) {
        margin-right: 5px;
    }
    &:last-child .page-link{
        margin-right: 0;
    }
    &:last-child .page-link{
        border-top-right-radius: 100%;
        border-bottom-right-radius: 100%;
    }
    &:first-child .page-link{
        border-top-left-radius: 100%;
        border-bottom-left-radius: 100%;
    }
    &:last-child{
        margin-right: 0;
    }
}
.page-link{
    width: 33px;
    height: 33px;
    text-align: center;
    line-height: 33px;
    border-radius: 100%;
    .icon-svg{
        vertical-align: -.125em;
        font-size: 10px;
    }
}
