.badge{
    text-transform: uppercase;
    line-height: 1.8;
    padding: 3px 15px;
    letter-spacing: 1px;
    @media (max-width: 575px) {
        padding: 3px 10px;
        letter-spacing: 0;
    }
}
.product{
    &-sort{
        margin-bottom: 20px;
        &__block{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            @media (max-width: 767px) {
                position: fixed;
                top: 0;
                left: 0;
                bottom: 0;
                z-index: 100;
                overflow: auto;
                right: 0;
                height: 100%!important;
                transition: all .25s ease-in-out;
                background: #fff;
                display: block;
                .close{
                    font-size: 2rem;
                    margin-right: 20px;
                    margin-top: 10px;
                }
                &.collapse:not(.show) {
                    left: -100%;
                    right: 100%;
                    display: block;
                }
            }
            &.collapse{
                @media (min-width: 992px) {
                    display: block;
                }
            }
        }
        &__label{
            //font-size: 12px;
            color: #636363;
            margin-right: 15px;
            @media (max-width: 767px) {
                padding: 20px 20px 0;
                font-weight: bold;
                margin-bottom: 18px;
                color: $black;
            }
        }
        &__list{
            list-style: none;
            padding: 0;
            display: flex;
            flex-wrap: wrap;
            @media (max-width: 767px) {
                padding: 0 20px 20px;
            }
            li{
                margin-right: 5px;
                padding: 6px 15px;
                border-radius: 15px;
                &:last-child{
                    margin-right: 0;
                }
                &.active{
                    background-color: #dadee6;
                    a{
                        color: $black;
                    }
                }
                a{
                    .icon-svg{
                        margin-left: 10px;
                    }
                    color: #636363;
                    &:hover{
                        .icon-svg{
                            fill: $orange;
                        }
                        text-decoration: none;
                        color: $orange;
                    }
                }
            }
        }
    }
    &-grid{
        margin-bottom: 37px;
        .row{
            margin: 0;
        }
        .col-xl-5{
            @media (min-width: 768px) and (max-width: 1229px) {
                flex: 0 0 33.3333%;
                max-width: 33.3333%;
            }
        }
        .product-grid__item{
            height: calc(100% - 1px);
            margin-right: 1px;
            margin-bottom: 1px;
        }
        &__item{
            background-color: $white;
            height: 100%;
            border: 1px solid transparent;
            transition: border-color .1s ease-in-out;
            &.stock_none{
                .product-grid__stock{
                    color: #bbb;
                    &:before{
                        background-color: #bbb;
                    }
                }
                .product-grid__buy{
                    border: none;
                    padding-left: 0;
                    padding-right: 0;
                }
            }
            &:hover{
                border: 1px solid $orange;
            }
        }
        &__top{
            padding: 15px 22px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 52px;
            @media (max-width: 575px) {
                padding: 5px 5px;
                height: 34px;
            }
        }
        &__stock{
            color: $success;
            font-weight: normal;
            line-height: 1.5;
            @media (max-width: 575px) {
                font-size: 12px;
            }
            &:before{
                content: " ";
                display: inline-block;
                border-radius: 100%;
                width: 4px;
                height: 4px;
                background-color: $success;
                vertical-align: middle;
                margin-right: 8px;
                @media (max-width: 575px) {
                    display: none;
                }
            }
            &_badge{
                border: 2px solid $success;
                line-height: 24px;
                border-radius: 14px;
                display: inline-block;
                padding: 0 22px;
                white-space: nowrap;
            }
        }
        &__img{
            padding: 0 22px;
            height: 140px;
            line-height: 140px;
            @media (max-width: 575px) {
                padding: 0 5px;
                height: 100px;
                line-height: 100px;
            }
            img{
                object-fit: cover;
                max-height: 100%;
                max-width: 100%;
                overflow: hidden;
                display: inline-block!important;
            }
            a{
                display: block;
                height: 100%;
                text-decoration: none;
            }
        }
        &__title{
            padding: 20px 22px 90px;
            font-size: 16px;
            line-height: 1.286;
            @media (max-width: 575px) {
                font-size: 14px;
                padding-left: 5px;
                padding-right: 5px;
                padding-bottom: 75px;
            }
            a{
                color: $black;
                &:hover{
                    color: $orange;
                }
            }
        }
        &__action{
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0 22px;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 25px;
            @media (max-width: 575px) {
                padding-left: 5px;
                padding-right: 5px;
            }
        }
        &__buy{
            border: 1px solid $orange;
            border-radius: 44px;
            height: 44px;
            padding-left: 20px;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            line-height: 1;
            @media (max-width: 575px) {
                padding-left: 10px;
                height: 34px;
            }
        }
        &__btn{
            margin-left: 6px;
            @media (max-width: 575px) {
                margin-left: 3px;
            }
            .btn{
                height: 44px;
                margin: -1px -1px 0 0;
                border-color: transparent;
                padding: 0 15px;
                font-size: 18px;
                fill: $orange;
                @media (max-width: 575px) {
                    padding: 0 6px;
                    height: 34px;
                }
                &:hover{
                    fill: $white;
                    background-color: $orange;
                }
            }
        }
        &__price{
            &_old{
                font-weight: normal;
                color: #676767;
                text-decoration: line-through;
                line-height: 1;
                width: 100%;
                @media (max-width: 575px) {
                    font-size: 12px;
                }
                +.product-grid__price_current{
                    color: $orange;
                }
            }
            &_current{
                font-size: 18px;
                width: 100%;
                font-weight: bold;
                line-height: 1.1;
                @media (max-width: 575px) {
                    font-size: 14px;
                }
            }
        }
    }
}
