body{
    font-family: 'Proxima Nova Rg', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 1.5;
    color: $black;
    background-color: #f7f8fa;
}
h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
    font-weight: bold;
}
h1, .h1{
    line-height: 1.235;
}
h3, .h3{
    line-height: 1.75;
}
h6, .h6{
    line-height: 2.125;
    margin-bottom: 12px;
}
.main-content{
    padding: 40px 0 48px;
    @media (max-width: 767px) {
        padding: 20px 0 28px;
    }
    h1, .h1{
        font-size: 28px;
        margin-bottom: 33px;
        @media (max-width: 767px) {
            font-size: 24px;
        }
    }
}
.breadcrumb{
    @media (max-width: 767px) {
        margin-bottom: 15px;
    }
    a{
        color: #b9b9b9;
    }
}
.section-header{
    margin-bottom: 35px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    .icon-svg{
        font-size: 10px;
    }
    .h3{
        line-height: 1;
        padding-right: 90px;
    }
}
.text-orange{
    color: $orange!important;
}
.icon-svg{
    vertical-align: middle;
    display: inline-block;
    &.icon-primary{
        svg{
            fill: $primary;
        }
    }
    &.icon-lg svg{
        height: 1.21em;
    }
    &.icon-sm svg{
        height: .667em;
    }
    svg{
        height: 1em;
        display: block;
    }
    &.rotate-90 svg{
        transform: rotate(90deg);
    }
    &.rotate-180 svg{
        transform: rotate(180deg);
    }
}
.white-block{
    background-color: $white;
    padding: 35px 30px 55px;
    border-bottom: 1px solid #f0f0f0;
    &:last-child{
        border-bottom: none;
    }
    @media (max-width: 575px) {
        padding: 15px 15px 35px;
    }
}
.link-more{
    color: #6d6f7b;
    padding: 0 0 3px 0;
    border-bottom: 1px dashed #6d6f7b;
    text-decoration: none;
    &:hover{
        color: $orange;
        text-decoration: none;
    }
    .icon-svg {
        font-size: 8px;
        margin-left: 10px;
    }
}
.btn-back{
    background-color: #e2e3ec;
    border-radius: 10px;
    font-size: 11px;
    padding: 6px 15px;
    display: inline-block;
    margin-right: 14px;
    vertical-align: middle;
    margin-bottom: 5px;
    .icon-svg{
        vertical-align: -.1em;
    }
    &:hover{
        background-color: #d7d8e6;
    }
}
.modal{
    &-content{
        border-radius: 0;
    }
    &-title{
        font-size: 24px;
        font-weight: bold;
        line-height: 1.333;
    }
    &-header{
        padding: 40px 50px 30px;
        border-bottom: none;
        .close {
            margin: -1.5rem -1.5rem -1rem auto;
            padding: 0;
            font-size: 35px;
        }
    }
    &-body{
        padding: 30px 50px 75px;
    }
    &-footer{
        justify-content: unset;
        border-top: 0;
        padding: 75px 50px 30px;
    }
}