.slider{
    padding: 30px 0 0;
    img{
        @media (max-width: 575px) {
            height: 200px;
            object-fit: cover;
            object-position: right;
        }
        @media (min-width: 576px) and (max-width: 767px) {
            height: 300px;
            object-fit: cover;
            object-position: right;
        }
    }
}
.owl-carousel{
    .owl-nav {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
    }
    &.nav_top-right{
        .owl-nav {
            position: absolute;
            top: -76px;
            left: unset;
            right: 0;
            display: flex;
            .owl-prev,
            .owl-next{
                font-size: 10px;
                background-color: #fff;
                padding: 13px 17px;
                fill: #afafaf;
                &:hover{
                    fill: $orange;
                }
            }
            .owl-prev{
                margin-right: 1px;
                border-top-left-radius: 41px;
                border-bottom-left-radius: 41px;
            }
            .owl-next{
                border-top-right-radius: 41px;
                border-bottom-right-radius: 41px;
            }
        }
    }
    .owl-stage{
        display: flex;
        margin-left: 1px;
    }
    .owl-item {
        flex-basis: auto;
        flex-shrink: 0;
    }
    .owl-dots{
        position: absolute;
        bottom: 18px;
        left: 0;
        right: 0;
        font-size: 0;
        line-height: normal;
        -ms-flex-pack: center;
        justify-content: center;
        display: -ms-flexbox;
        display: flex;
    }
    &__item{
        overflow: hidden;
        height: 100%;
    }
    .owl-dot{
        margin-right: 15px;
        &.active span{
            transform: scale(2.5);
        }
        &:last-child{
            margin-right: 0;
        }
        span{
            width: 4px;
            height: 4px;
            background: $white;
            display: block;
            border-radius: 100%;
        }
    }
    &-thumbs{
        list-style: none;
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: center;
        li{
            width: 80px;
            height: 80px;
            line-height: 80px;
            border: 1px solid #dee0e2;
            margin-right: 9px;
            &:last-child{
                margin-right: 0;
            }
            &.active{
                border-color: #24aade;
            }
            a{
                display: block;
            }
            img{
                max-width: 100%;
                max-height: 100%;
            }
        }
    }
}
