.filter{
    background-color: $white;
    @media (max-width: 767px) {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        z-index: 100;
        overflow: auto;
        right: 0;
        height: 100%!important;
        transition: all .25s ease-in-out;
        .close{
            font-size: 2rem;
            margin-right: 20px;
            margin-top: 10px;
        }
        &.collapse:not(.show) {
            left: -100%;
            right: 100%;
            display: block;
        }
    }
    &-group{
        border-bottom: 1px solid #f4f4f4;
        padding: 20px;
        &:last-child{
            border-bottom: none;
        }
    }
    &-label{
        font-weight: bold;
        margin-bottom: 18px;
    }
    &-more{
        //font-size: 12px;
        color: rgb(99, 99, 99);
        text-decoration: underline;
        line-height: 2.5;
    }
    .btn{
        margin-top: 35px;
        .icon-svg{
            fill: $black;
        }
        &:hover .icon-svg{
            fill: $white;
        }
    }
    &.collapse{
        @media (min-width: 992px) {
            display: block;
        }
    }
}
