.header{
    background-color: $white;
    &-top{
        border-bottom: 1px solid #f4f4f4;
        @media (max-width: 767px) {
            padding: 5px 0;
            border-bottom: none;
        }
        .logo{
            @media (max-width: 767px) {
                margin-left: 5px;
            }
            img{
                @media (max-width: 767px) {
                    height: 40px;
                }
            }
        }
    }
    &-middle{
        padding: 20px 0 26px;
        &__address{
            //font-size: 12px;
        }
        &__phone{
            font-size: 20px;
            font-weight: bold;
            line-height: 0.89;
            color: $black;
            &:hover{
                color: $black;
            }
        }
        &__email{
            line-height: 1.286;
            color: $black;
            .icon-svg{
                fill: $blue;
                margin-right: 8px;
            }
            &:hover{
                color: $black;
            }
        }
    }
    &-bottom{
        border-top: 1px solid #f4f4f4;
        position: relative;
        @media (max-width: 767px) {
            padding-bottom: 8px;
        }
        &__catalog{
            &-btn{
                font-size: 16px;
                color: $black;
                font-weight: bold;
                line-height: 1.286;
                display: block;
                position: relative;
                padding: 14px 11px;
                //&:hover,
                &.active{
                    & + .catalog-dropdown{
                        display: block;
                    }
                }
                //&:hover:before,
                &.active:before{
                    display: block;
                }
                &:before{
                    content: " ";
                    display: none;
                    position: absolute;
                    border-left: 10px solid transparent;
                    border-right: 10px solid transparent;
                    border-bottom: 10px solid #f1f1f1;
                    left: 9px;
                    bottom: 0;
                }
                @media (max-width: 767px) {
                    &:before{
                        content: " ";
                        display: block;
                        position: absolute;
                        border-bottom: none;
                        border-left: 5px solid transparent;
                        border-right: 5px solid transparent;
                        border-top: 5px solid $black;
                        left: unset;
                        bottom: unset;
                        right: 0;
                        top: 50%;
                        margin-top: -2px;
                    }
                    &.active:before{
                        border-top: none;
                        border-left: 5px solid transparent;
                        border-right: 5px solid transparent;
                        border-bottom: 5px solid $black;
                    }
                }
                &:hover{
                    text-decoration: none;
                    color: $orange;
                }
            }
        }
        &__auth{
            //font-size: 12px;
            .icon-svg{
                margin-right: 8px;
            }
            a{
                color: $black;
                line-height: 1.333;
                margin-right: 4px;
                &:last-child{
                    margin-right: 0;
                    margin-left: 4px;
                }
            }
        }
        &__search{
            &-input{
                border: none;
                //font-size: 12px;
                font-size: 14px;
                height: 38px;
                @media (max-width: 767px) {
                    border: 1px solid #ced4da;
                }
            }
            &-btn{
                width: 38px;
                height: 38px;
                background-color: transparent;
                border: none;
                margin-left: -38px;
                font-size: 16px;
            }
        }
        &__favorite{
            width: 50px;
            height: 49px;
            text-align: center;
            line-height: 46px;
            border-left: 1px solid #f4f4f4;
            border-right: 1px solid #f4f4f4;
            font-size: 18px;
            &:hover .icon-svg{
                fill: $orange;
            }
            @media (max-width: 767px) {
                border: none;
            }
        }
        &__cart{
            a{
                text-decoration: none;
                color: $black;
                &:hover{
                    color: $orange;
                }
            }
            .icon-svg{
                font-size: 25px;
                margin-right: 8px;
            }
            &-count{
                //font-size: 14px;
                font-size: 16px;
                font-weight: bold;
                //line-height: 1.286;
                line-height: 1.1;
            }
            &-counter{
                color: $white;
                width: 18px;
                height: 18px;
                border: 2px solid $white;
                border-radius: 100%;
                background-color: $orange;
                margin-right: -6px;
                font-size: 12px;
                z-index: 1;
                text-align: center;
                line-height: 16px;
            }
            &-sum{
                //font-size: 12px;
                color: #353535;
                line-height: 1.1;
            }
        }
    }
}
.catalog{
    &-dropdown{
        display: none;
        position: absolute;
        left: 0;
        right: 0;
        z-index: 1000;
        background-color: #f1f1f1;
        overflow: hidden;
        @media (max-width: 767px) {
            margin-bottom: 8px;
            position: static;
        }
    }
    &-menu{
        list-style: none;
        margin: 0;
        padding: 52px 0 52px 20px;
        width: 280px;
        @media (max-width: 767px) {
            padding: 0;
            width: unset;
        }
        & > li{
            margin-bottom: 0;
            line-height: 2.8;
            @media (max-width: 767px) {
                border-bottom: 1px solid #dfdfdf;
                &:last-child{
                    border-bottom: none;
                }
            }
            &:last-child{
                margin-bottom: 0;
            }
            & > a{
                color: $black;
                display: block;
                position: relative;
                &.active{
                    & + .catalog-sub-dropdown{
                        display: block;
                    }
                }
                //&:hover,
                &.active{
                    color: $orange;
                    &:after{
                        content: " ";
                        display: block;
                        position: absolute;
                        border-top: 10px solid transparent;
                        border-right: 10px solid #dfdfdf;
                        border-bottom: 10px solid transparent;
                        right: 0;
                        top: 50%;
                        margin-top: -10px;
                        @media (max-width: 767px) {
                            display: none;
                        }
                    }
                }
            }
        }

    }
    &-sub-dropdown{
        position: absolute;
        left: 290px;
        width: calc(100% - 300px);
        height: 100%;
        top: 0;
        display: none;
        padding: 47px 0 47px 60px;
        background-color: #dfdfdf;
        @media (max-width: 767px) {
            position: static;
            left: unset;
            width: unset;
            height: unset;
            top: unset;
            padding: 16px 0 0 24px;
        }
    &:after{
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        height: 100%;
        width: 200%;
        content: " ";
        background-color: #dfdfdf;
        z-index: -1;
        @media (max-width: 767px) {
            display: none;
        }
    }
    .h6{
        line-height: 1;
        margin-bottom: 24px;
        @media (max-width: 767px) {
            display: none;
        }
    }
}
&-sub-menu{
    list-style: none;
    margin: 0;
    padding: 0;
    & > li{
        margin-bottom: 16px;
        line-height: 1.5;
        &:last-child{
            margin-bottom: 0;
        }
        &:hover{
            .catalog-sub-dropdown{
                display: block;
            }
        }
        & > a{
            display: block;
            color: $black;
            &:hover{
                color: $orange;
                text-decoration: none;
                .icon-svg{
                    fill: $orange;
                }
            }
            .icon-svg{
                font-size: 6px;
                margin-left: 10px;
            }
        }
    }
}
}
.navbar{
&-nav {
    //font-size: 12px;
    line-height: 1.5;
    margin-bottom: -1px;
    .nav{
        &-item{
            margin-right: 18px;
            &:last-child{
                margin-right: 0;
            }
            @media (max-width: 1229px) {
                margin-right: 8px;
            }
        }
        &-link{
            color: #666;
            border-bottom: 1px solid #f4f4f4;
            &:hover,
            &.active{
                color: $orange;
                border-bottom-color: $orange;
            }
        }
    }
}
&-toggler-icon {
    .bars{
        width: 100%;
        margin-right: 0;
        display: block;
        &__line{
            height: 3px;
            width: 100%;
            margin: 5px 0;
        }
    }
}
}
.dropdown{
&-menu{
    background-color: #e2e3ec;
    border-radius: 5px;
    border: none;
    margin: 0;
    font-size: unset;
    padding: 10px 0;
}
&-item{
    padding: 5px 26px;
    &:focus, &:hover{
        background-color: transparent;
        color: $orange;
        text-decoration: underline;
    }
}
&-toggle::after {
    vertical-align: -0.30em;
    content: "";
    border: none;
    background-color: #e2e3ec;
    background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="6px" height="3px"><path fill-rule="evenodd"  fill="rgb(139, 142, 167)" d="M3.000,3.000 L0.009,-0.000 L5.991,-0.000 L3.000,3.000 Z"/></svg>');
    background-position: center;
    background-repeat: no-repeat;
    padding: 0;
    width: 24px;
    height: 15px;
    border-radius: 15px;
}
}
@media (min-width: 992px) {
.navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 9px;
    padding-left: 9px;
}
}
.location{
//font-size: 12px;
margin-left: 15px;
a{
    color: #0079a8;
    text-decoration: underline;
    line-height: 1.333;
}
.icon-svg{
    margin-right: 8px;
}
}
.logo{
margin-left: 15px;
}
.bars{
margin-right: 24px;
width: 16px;
display: inline-block;
&__line{
    display: block;
    background-color: $orange;
    height: 2px;
    margin-bottom: 2px;
    &:last-child{
        margin-bottom: 0;
    }
}
}
