.catalog-grid{
    padding: 40px 0 0;
    margin-bottom: -20px;
    .section-header{
        margin-bottom: 30px;
    }
    &__item{
        margin-bottom: 20px;
        height: calc(100% - 20px);
        background-color: $white;
        &:hover{
            .catalog-grid__img img{
                transform: scale(1.05);
            }
        }
        &.catalog-grid__item_horizontal{
            display: flex;
            .catalog-grid__media{
                width: 50%;
                height: 100%;
            }
            .catalog-grid__img{
                height: 250px;
                padding: 40px 0;
            }
            .catalog-grid__body{
                width: 50%;
                position: relative;
                &:before{
                    content: " ";
                    display: block;
                    width: 20px;
                    height: 20px;
                    border-top: 10px solid transparent;
                    border-bottom: 10px solid transparent;
                    border-right: 10px solid #fff;
                    position: absolute;
                    left: -20px;
                    bottom: 50px;
                }
            }
            .catalog-grid__title{
                bottom: 45px;
            }
        }
        &.catalog-grid__item_vertical{
            .catalog-grid__media{
                height: 100%;
            }
            .catalog-grid__img{
                height: 250px;
                padding: 40px 0;
            }
            .catalog-grid__title{
                bottom: 45px;
            }
        }
    }
    &__img{
        padding: 12px 0 52px;
        align-items: center;
        display: flex;
        justify-content: center;
        img{
            max-width: 100%;
            transition: transform .25s ease-in-out;
        }
    }
    &__media{
        text-align: center;
        position: relative;
    }
    &__title{
        padding: 0 30px;
        font-size: 16px;
        font-weight: bold;
        line-height: 1;
        position: absolute;
        bottom: 25px;
        left: 0;
        right: 0;
        height: 32px;
        display: flex;
        justify-content: center;
        align-items: center;
        a{
            color: $white;
            &:hover{
                color: $orange;
            }
        }
    }
    &__body{
        padding: 25px 30px;
    }
    &__menu{
        list-style: none;
        margin: 0;
        padding: 0;
        li{
            margin-bottom: 16px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        a{
            color: $black;
            &:hover{
                color: $orange;
            }
        }
    }
}
.radial-gradient{
    &__green{
        background-image: radial-gradient( 50% 50%, rgb(218,239,210) 0%, rgb(157,214,137) 100%);
    }
    &__sky{
        background-image: radial-gradient( 50% 50%, rgb(221,246,255) 0%, rgb(146,204,226) 100%);
    }
    &__orange{
        background-image: radial-gradient( 50% 50%, rgb(255,184,168) 0%, rgb(255,145,107) 100%);
    }
    &__purple{
        background-image: radial-gradient( 50% 50%, rgb(213,202,242) 0%, rgb(167,146,226) 100%);
    }
    &__yellow{
        background-image: radial-gradient( 50% 50%, rgb(255,238,203) 0%, rgb(244,181,90) 100%);
    }
    &__blue{
        background-image: radial-gradient( 50% 50%, rgb(186,188,255) 0%, rgb(130,133,255) 100%);
    }
}
