footer{
    background-color: #353742;
    //font-size: 12px;
}
.footer{
    &-description{
        color: #c6c6c6;
        margin-top: 24px;
        @media (max-width: 767px) {
            margin-top: 12px;
        }
        i{
            color: #5c5f6e;
        }
    }
    &-top{
        padding: 35px 0;
        .btn-outline-primary{
            color: $white;
        }
    }
    &-menu{
        list-style: none;
        margin: 24px 0 0;
        padding: 0;
        @media (max-width: 767px) {
            margin-top: 0;
        }
        li{
            margin-bottom: 16px;
            @media (max-width: 767px) {
                margin-bottom: 5px;
            }
            &:last-child{
                margin-bottom: 0;
            }
        }
        a{
            color: #c3c3c3;
            &:hover{
                color: $orange;
                text-decoration: none;
            }
        }
    }
    &-phone{
        font-size: 22px;
        font-weight: bold;
        margin-top: 30px;
        @media (max-width: 767px) {
            margin-top: 12px;
        }
        a{
            color: $white;
            &:hover{
                color: $white;
            }
        }
    }
    &-email{
        margin-top: 24px;
        @media (max-width: 767px) {
            margin-top: 12px;
        }
        .icon-svg{
            fill: $blue;
            margin-right: 8px;
        }
        a{
            color: #c6c6c6;
            font-size: 14px;
            line-height: 1.286;
            &:hover{
                color: #c6c6c6;
            }
        }
    }
    &-social{
        margin-bottom: 35px;
        @media (max-width: 767px) {
            margin-bottom: 15px;
        }
        &__item{
            background-color: #2f303b;
            border-radius: 100%;
            width: 38px;
            height: 38px;
            text-align: center;
            line-height: 34px;
            display: inline-block;
            margin-right: 11px;
            font-size: 18px;
            .icon-svg{
                fill: #9da2c1;
            }
            &:hover .icon-svg{
                fill: $orange;
            }
            &:last-child{
                margin-right: 0;
            }
        }
    }
    &-payment{
        @media (max-width: 767px) {
            margin-top: -20px;
            margin-bottom: -10px;
        }
        &__item{
            display: inline-block;
            margin-right: 11px;
            font-size: 42px;
            .icon-svg{
                fill: #9da2c1;
            }
            &:last-child{
                margin-right: 0;
            }
            @media (max-width: 1229px) {
                 font-size: 30px;
            }
        }
    }
}
.copyright{
    border-top: 1px solid #2e303a;
    color: #c6c6c6;
    padding: 20px 0;
    line-height: 1.6;
    a{
        color: #ad24b6;
    }
}
