.product-full{
    &__slider{
        padding: 0 20px;
    }
    &__main{
        background-color: $white;
        padding: 40px 0 16px;
        margin-bottom: 18px;
        h1, .h1{
            font-size: 26px;
            line-height: 1.273;
            margin-bottom: 28px;
        }
    }
    &__info{
        padding-right: 40px;
        @media (max-width: 767px) {
            padding-right: 27px;
        }
        @media (max-width: 991px) {
            padding-left: 27px;
        }
        @media (min-width: 767px) and (max-width: 1229px) {
            padding-right: 0;
        }
    }
    &__description{
        //font-size: 12px;
        color: #7d7d7d;
        line-height: 1.333;
    }
    &__equipment{
        margin-top: 42px;
        @media (max-width: 1229px) {
            margin-top: 21px;
        }
    }
    &__price{
        display: flex;
        align-items: baseline;
        line-height: 18px;
        margin: 50px 0 28px;
        @media (max-width: 767px) {
            margin-top: 55px;
        }
        @media (min-width: 768px) and (max-width: 1229px) {
            margin-top: 25px;
        }
        &_old{
            font-size: 16px;
            color: $black;
            margin-right: 10px;
            text-decoration: line-through;
            @media (max-width: 767px) {
                margin-right: 5px;
                font-size: 12px;
            }
        }
        &_current{
            font-size: 26px;
            font-weight: bold;
            color: $red;
            @media (max-width: 767px) {
                margin-right: 5px;
                font-size: 22px;
            }
        }
    }
    &__extra{
        padding: 40px 27px 16px;
        border-left: 1px solid #ebedf0;
        height: calc(100% + 56px);
        margin: -40px 0 -16px;
        @media (max-width: 991px) {
            margin-top: 0;
            height: calc(100% + 16px);
        }
    }
    &__line{
        border-top: 1px solid #ebedf0;
        margin: 30px -27px;
    }
    &__vendor{
        a{
            display: block;
        }
        margin-bottom: 29px;
    }
    &__action{
        .btn{
            font-size: 14px;
            font-weight: bold;
            .icon-svg{
                fill: $white;
                font-size: 20px;
                margin-right: 20px;
                margin-top: -2px;
            }
        }
        .add-favorite{
            text-decoration: none;
            //font-size: 12px;
            color: $black;
            margin-left: 15px;
            &:hover{
                color: $orange;
            }
            .icon-svg{
                font-size: 18px;
                margin-right: 10px;
            }
        }
    }

    &__tabs{
        .nav-tabs{
            border-bottom: 2px solid #f7f8fa;
            background-color: $white;
            padding: 0 25px;
            @media (max-width: 767px) {
                display: none;
            }
            .nav-item{
                margin-bottom: -2px;
                margin-right: 10px;
                &:last-child{
                    margin-right: 0;
                }
                @media (max-width: 767px) {
                    width: 100%;
                    margin-right: 0;
                }
            }
            .nav-link{
                padding: 19px 24px;
                @media (max-width: 991px) {
                    padding: 19px 5px;
                }
                border: none;
                background-color: transparent;
                font-size: 14px;
                color: #8c8c8c;
                line-height: 1;
                border-bottom: 2px solid transparent;
                &.active{
                    color: $black;
                    border-bottom: 2px solid $orange;
                }
            }
        }
        .tab-content{
            .tab-pane{
                padding: 45px 24px;
                background-color: $white;
            }
        }
    }
}
.tab-switch{
    padding: 19px 24px;
    font-size: 14px;
    color: #8c8c8c;
    line-height: 1;
    border-bottom: 2px solid #f7f8fa;
    background-color: $white;
    display: none;
    &:hover,
    &:focus{
        text-decoration: none;
    }
    &.active{
        display: none;
        color: $black;
        border-bottom: 2px solid $orange;
    }
    @media (max-width: 767px) {
        display: block;
        &.active {
            display: block;
        }
    }
}
.specs{
    &-value{
        font-weight: bold;
        @media (max-width: 767px) {
            float: none!important;
            display: block;
        }
    }
    &-item{
        background-color: #f0f0f0;
        padding: 7px 20px;
        margin-bottom: 5px;
        &:last-child{
            margin-bottom: 0;
        }
    }
}
.payment-list{
    margin: 30px 0 0;
    line-height: 2.333;
    list-style: none;
    padding: 0;
    .icon-svg{
        font-size: 16px;
        margin-right: 11px;
    }
    a{
        color: #4c4c4c;
        text-decoration: underline;
        &:hover{
            color: $orange;
        }
    }
}
.delivery-list{
    list-style: none;
    padding: 0;
    margin: 0;
    li{
        display: flex;
        justify-content: space-between;
        position: relative;
        span{
            background-color: $white;
            z-index: 1;
            padding-right: 2px;
        }
        &:before{
            content: " ";
            display: block;
            position: absolute;
            left: 0;
            right: 0;
            bottom: 6px;
            border-bottom: 1px dotted $black;
        }
        b{
            padding-left: 2px;
            background-color: $white;
            z-index: 1;
        }
    }
}
.delivery-location{
    .icon-svg{
        font-size: 16px;
        margin-right: 11px;
    }
    &__city{
        font-size: 14px;
        line-height: 1.286;
        margin-top: 2px;
        display: inline-block;
        a{
            color: #1ba6dc;
            text-decoration: underline;
            &:hover{
                color: $orange;
            }
        }
        margin-bottom: 16px;
    }
}
.add-favorite{
    fill: #9e9e9e;
    font-size: 15px;
    &:hover{
        fill: $orange;
    }
    &.active{
        fill: $orange;
    }
}
