.news{
    padding: 65px 0 80px;
    margin-bottom: -20px;
    &__item{
        background-color: $white;
        height: calc(100% - 20px);
        position: relative;
        margin-bottom: 20px
    }
    &__img{
        background-color: #e2e3ec;
        position: relative;
        &:after{
            content: " ";
            border-bottom: 24px solid #dde1e9;
            border-left: 24px solid transparent;
            height: 24px;
            width: 24px;
            display: block;
            right: 0;
            bottom: 0;
            position: absolute;
        }
        img{
            object-fit: cover;
            width: 100%;
            overflow: hidden;
        }
        a{
            display: block;
            text-decoration: none;
        }
    }
    &__title{
        font-weight: bold;
        line-height: 1.571;
        margin-bottom: 0;
        padding: 25px 30px 51px;
        a{
            color: $black;
            &:hover{
                color: $orange;
            }
        }
    }
    &__date{
        //font-size: 12px;
        color: #999999;
        line-height: 1.833;
        position: absolute;
        bottom: 15px;
        padding: 0 30px;
    }
}
