.btn{
    white-space: nowrap;
}
.btn-outline-primary{
    color: #666;
    .icon-svg{
        fill: $primary;
        margin-right: 8px;
    }
    &:hover .icon-svg{
        fill: $white;
    }
    &.btn-sm{
        color: $black;
    }
}
.slider-range{
    padding: 15px 0;
    width: 100%;
    &-input{
        display: flex;
        align-items: center;
        padding-top: 0;
        flex-wrap: wrap;
        .slider-range-item{
            display: flex;
            flex-grow: 1;
            width: 100%;
            order: 0;
        }
        input{
            display: flex;
            flex-grow: 0;
            width: calc(50% - 5px);
            margin-right: 5px;
            text-align: center;
            order: 1;
            & + input{
                margin-left: 5px;
                margin-right: 0;
                order: 2;
            }
        }
    }
}
.ui-slider{
    height: 4px;
    border-radius: 4px;
    position: relative;
    margin: 10px 0 24px;
    background: #d0d0d0;
    .ui-slider-handle{
        height: 15px;
        width: 15px;
        border-radius: 100%;
        position: absolute;
        z-index: 2;
        margin-left: -7.5px;
        top: -5px;
        transition: none;
        border: 4px solid $orange;
        background-color: $white;
        outline: none;
        &:before{
            display: none;
        }
    }
    &-horizontal .ui-slider-range{
        height: 100%;
        position: absolute;
        z-index: 1;
        display: block;
        transition: none;
        top: 0;
        border-radius: 5px;
        background-color: $orange;
    }
}
.form-check{
    padding-left: 30px;
    margin-bottom: 12px;
    &.form-check-slide{
        padding-left: 64px;
        input[type="checkbox"]{
            & + label{
                position: relative;
                &:before{
                    width: 48px;
                    height: 24px;
                    margin-left: -64px;
                    top: -4px;
                    border-radius: 12px;
                    background-color: #edeff2;
                    border: none;
                }
                &:after{
                    content: '';
                    position: absolute;
                    width: 16px;
                    height: 16px;
                    background-color: $white;
                    border-radius: 100%;
                    left: -59px;
                }
            }
            &:checked + label{
                &:before{
                    background-color: $success;
                    background-image: none;
                }
                &:after{
                    left: -37px;
                }
            }
        }
    }
    input[type="checkbox"]{
        position: absolute;
        z-index: -1;
        opacity: 0;
        & + label{
            //font-size: 12px;
            position: relative;
            &:before{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 20px;
                height: 20px;
                border: 1px solid #d0d0d0;
                border-radius: 6px;
                margin-left: -30px;
            }
        }
        &:checked + label:before{
            background-image: url('data:image/svg+xml,<svg fill="%23ff6441" viewBox="0 -46 417.81333 417" xmlns="http://www.w3.org/2000/svg"><path d="m159.988281 318.582031c-3.988281 4.011719-9.429687 6.25-15.082031 6.25s-11.09375-2.238281-15.082031-6.25l-120.449219-120.46875c-12.5-12.5-12.5-32.769531 0-45.246093l15.082031-15.085938c12.503907-12.5 32.75-12.5 45.25 0l75.199219 75.203125 203.199219-203.203125c12.503906-12.5 32.769531-12.5 45.25 0l15.082031 15.085938c12.5 12.5 12.5 32.765624 0 45.246093zm0 0"/></svg>');
            background-size: 10px;
            background-position: center;
            background-repeat: no-repeat;
            border-color: $orange;
        }
    }
}
.icon-question{
    fill: $orange;
    margin-left: 9px;
}
.bs-tooltip-bottom .arrow:before{
    border-bottom-color: #edeff2;
}
.tooltip-inner{
    background-color: #edeff2;
    //font-size: 12px;
    color: rgb(130, 130, 130);
    line-height: 1.667;
    padding: 30px 22px;
    text-align: left;
}
.form-control-sm{
    border-radius: 15px;
    color: $black;
}
.form-control-lg{
    height: 50px;
    color: #000;
    border-radius: 10px;
    font-size: 16px;
    border-color: #bec6d6;
    padding-left: 26px;
    padding-right: 26px;
    &:focus, &:active{
        border-color: #9ea9bf;
        box-shadow: none;
    }
    &::placeholder{
        $color: #c0c0c0;
    }
}
.custom-file{
    max-width: 280px;
    &-lg{
        height: 50px;
        .custom-file-label{
            height: 50px;
        }
    }
    &-label{
        border-radius: 10px;
        border-style: dashed;
        border-color: #bec6d6;
        font-size: 16px;
        line-height: 50px;
        padding-top: 0;
        padding-bottom: 0;
        color: #c0c0c0;
        text-align: center;
        .icon-svg{
            fill: #cbcfd7;
            font-size: 21px;
            margin-right: 25px;
        }
        &:focus, &:active{
            border-color: #9ea9bf;
            box-shadow: none;
        }
        &:after{
            display: none;
        }
    }
}
label span{
    //font-size: 12px;
    color: #7c7e88;
    font-style: italic;
}
.chosen-container{
    font-size: 14px;
    &-single .chosen-single{
        background-image: none;
        background-color: transparent;
        color: $black;
        box-shadow: none;
        border: 2px solid #cfd3da;
        border-radius: 21px;
        height: 42px;
        padding: 0 21px;
        display: flex;
        align-items: center;
        div {
            width: 35px;
            b{
                transform: rotate(90deg);
                background-image: url('data:image/svg+xml,<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 330 330" style="enable-background:new 0 0 330 330;" xml:space="preserve"><path id="XMLID_222_" d="M250.606,154.389l-150-149.996c-5.857-5.858-15.355-5.858-21.213,0.001 c-5.857,5.858-5.857,15.355,0.001,21.213l139.393,139.39L79.393,304.394c-5.857,5.858-5.857,15.355,0.001,21.213 C82.322,328.536,86.161,330,90,330s7.678-1.464,10.607-4.394l149.999-150.004c2.814-2.813,4.394-6.628,4.394-10.606 C255,161.018,253.42,157.202,250.606,154.389z"/></svg>');
                background-size: 9px;
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }
    &-active.chosen-with-drop .chosen-single {
        border: 2px solid #cfd3da;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
        background-image: none;
        box-shadow: none;
        div {
            b{
                background-position: center;
            }
        }
    }
    .chosen-drop{
        border: 2px solid #cfd3da;
        border-top: none;
        margin-top: -2px;
        box-shadow: none;
    }
    .chosen-results {
        color: $black;
        margin: 0;
        padding: 0;
        li{
            padding: 10px 21px;
        }
        li.highlighted{
            background-color: $orange;
            background-image: none;
        }
    }
}
.quantity-input{
    display: flex;
    border: 2px solid #cfd3da;
    border-radius: 21px;
    height: 42px;
    width: 109px;
    @media (max-width: 767px) {
        width: 79px;
    }
    &__value{
        width: 35px;
        border: none;
        height: 100%;
        padding: 0;
        text-align: center;
        @media (max-width: 767px) {
            width: 25px;
        }
    }
    &__button{
        width: 35px;
        height: 100%;
        line-height: 36px;
        text-align: center;
        font-size: 9px;
        fill: #9d9d9d;
        cursor: pointer;
        padding: 0;
        border: none;
        background-color: transparent;
        outline: none;
        @media (max-width: 767px) {
            width: 25px;
        }
        &:active, &:focus {
            outline: none;
        }
        &:hover{
            fill: $black;
        }
    }
}
