.cart{
    background-color: $white;
    padding: 0 0 38px 0;
    margin-bottom: 50px;
    &-item{
        display: flex;
        border-bottom: 1px solid #f0f0f0;
        padding: 4px 24px;
        align-items: center;
        justify-content: space-between;
        flex-wrap: nowrap;
        @media (max-width: 768px){
            flex-wrap: wrap;
        }
        &:hover{
            .cart-item__remove .btn{
                visibility: visible;
            }
        }
        &__img{
            height: 130px;
            margin-right: 42px;
            min-width: 114px;
            @media (max-width: 1229px) {
                height: auto;
                margin-right: 20px;
                flex: 0 0 114px;
            }
            img{
                object-fit: none;
                width: 100%;
                overflow: hidden;
                display: inline-block!important;
            }
            a{
                display: block;
                height: 100%;
                text-decoration: none;
            }
        }
        &__title{
            line-height: 1.286;
            font-weight: bold;
            flex-grow: 1;
            margin-right: 30px;
            max-width: 260px;
            @media (max-width: 768px){
                flex: 0 0 calc(100% - 134px);
                max-width: unset;
                margin-bottom: 15px;
                margin-right: 0;
            }
            @media (min-width: 768px) and (max-width: 1229px) {
                flex: 0 0 220px;
            }
            a{
                color: $black;
                &:hover{
                    color: $orange;
                }
            }
        }
        &__info{
            margin-top: 22px;
            @media (max-width: 768px){
                margin-top: 5px;
            }
            &>div{
                display: inline-block;
                margin-right: 20px;
                &:last-child{
                    margin-right: 0;
                }
            }
        }
        &__quantity{
            margin-left: 40px;
            margin-right: 50px;
            flex-grow: 1;
            @media (max-width: 1229px) {
                margin-left: 0;
                margin-right: 0;
            }
        }
        &__prices{
            white-space: nowrap;
            margin-right: 40px;
            @media (max-width: 768px){
                margin-right: 0;
            }
            @media (min-width: 767px) and (max-width: 1229px) {
                margin-right: 20px;
            }
        }
        &__price{
            display: flex;
            align-items: baseline;
            line-height: 18px;
            flex-wrap: wrap;
            justify-content: flex-end;
            &_item{
                width: 100%;
                text-align: right;
                color: #313131;
            }
            &_old{
                font-size: 14px;
                color: $black;
                margin-right: 10px;
                text-decoration: line-through;
                &+.cart-item__price_current{
                    color: $red;
                }
            }
            &_current{
                font-size: 18px;
                font-weight: bold;
                color: $black;
            }
        }
        &__remove{
            .btn{
                fill: #b8b8b8;
                visibility: hidden;
                &:hover{
                    fill: $orange;
                }
            }
        }
    }
}
