.product-slider{
    padding: 65px 0 80px;
    background-color: #f0f0f0;
    .section-header{
        @media (max-width: 991px) {

        }
    }
    .nav-tabs {
        border: none;
        display: block;
        margin-left: auto;
        padding-right: 145px;
        @media (max-width: 767px) {
            padding-right: 100px;
            margin-left: 0;
            margin-bottom: -30px;
            width: 100%;
        }
        @media (min-width: 768px) and (max-width: 991px) {
            padding-right: 0;
            margin-left: 0;
            margin-bottom: -30px;
        }
        @media (min-width: 992px) and (max-width: 1229px) {
            padding-right: 110px;
        }
        a{
            //font-size: 12px;
            line-height: 1.5;
            color: #878787;
            margin-right: 50px;
            &:hover{
                text-decoration: none;
                color: $orange;
            }
            &:last-child{
                margin-right: 0;
            }
            &.active{
                color: $black;
                border-bottom: 2px solid $orange;
            }
            @media (max-width: 1229px) {
                margin-right: 20px;
            }
        }
    }
}
