.applicability{
    padding: 40px 0 70px;
    margin-bottom: -20px;
    .section-header{
        margin-bottom: 30px;
    }
    &__item{
        margin-bottom: 20px;
        height: calc(100% - 20px);
        position: relative;
        a{
            display: block;
            &:hover .applicability__title{
                text-decoration: underline;
            }
            &:hover .applicability__count{
                bottom: 10px;
            }
        }
    }
    &__img{
        background-color: #e2e3ec;
        position: relative;
        &:after{
            content: " ";
            border-bottom: 24px solid $orange;
            border-left: 24px solid transparent;
            height: 24px;
            width: 24px;
            display: block;
            right: 0;
            bottom: 0;
            position: absolute;
        }
        img{
            object-fit: cover;
            width: 100%;
            overflow: hidden;
        }
        a{
            display: block;
            text-decoration: none;
        }
    }
    &__title{
        position: absolute;
        top: 20px;
        padding: 0 32px;
        color: $white;
        line-height: 1.714;
        @media (max-width: 1229px) {
            padding: 0 20px;
            line-height: 1.3;
        }
    }
    &__count{
        font-size: 46px;
        position: absolute;
        bottom: 0;
        padding: 0 32px;
        color: $white;
        line-height: 25px;
        transition: bottom .25s ease-in-out;
        @media (max-width: 1229px) {
            padding: 0 20px;
        }
    }
}
