.cheque{
    background-color: #353742;
    border-radius: 10px;
    padding: 24px 10px;
    color: #aaafc7;
    &-item{
        line-height: 18px;
        padding: 0 18px;
        margin-bottom: 16px;
        &:last-child{
            margin-bottom: 0;
        }
        &>.cheque-price{
            margin-left: 8px;
        }
    }
    &-price{
        color: $white;
        font-weight: bold;
        font-size: 16px;
        &_big{
            font-size: 20px;
        }
    }
    &-dash{
        border: 1px dashed #525563;
        height: 1px;
        margin: 32px 12px;
    }
    .btn{
        margin-top: 25px;
        font-size: 14px;
        font-weight: bold;
        fill: $white;
        .icon-svg{
            vertical-align: -.125em;
            margin-left: 16px;
        }
    }
    &-info{
        //font-size: 12px;
        color: #b6bac3;
        line-height: 1.5;
        margin-top: 25px;
        padding: 0 6px;
        a{
            color: #8b95a8;
            text-decoration: underline;
            &:hover{
                color: $orange;
            }
        }
    }
}
.order{
    &-detail {
        margin-top: 35px;
        padding: 45px;
        background-color: #f3f4f6;
        border-radius: 10px;
        @media (max-width: 575px) {
            padding: 15px;
        }
    }
    &-item{
        color: #6c6f7f;
        margin-bottom: 20px;
        @media (max-width: 575px) {
            display: flex;
            justify-content: space-between;
        }
        &:last-child{
            margin-bottom: 0;
        }
        & > .order-value{
            margin-left: 8px;
            @media (max-width: 575px) {
                display: block;
                margin-left: 0;
                text-align: right;
            }
        }
    }
    &-value{
        color: #000;
    }
}
.question{
    background-color: #edeff2;
    padding: 25px 22px;
    border-radius: 10px;
    &-title{
        font-weight: bold;
        font-size: 16px;
        margin-bottom: 14px;
    }
    &-text{
        color: #828282;
        line-height: 20px;
        //font-size: 12px;
    }
}

.toggler{
    border: 2px solid #eaedf2;
    color: #828282;
    line-height: 34px;
    display: inline-block;
    border-radius: 19px;
    margin-bottom: 30px;
    @media (max-width: 767px) {
        width: 100%;
    }
    &-item{
        cursor: pointer;
        padding: 0 60px;
        @media (max-width: 767px) {
            width: calc(100% + 4px);
        }
        @media (max-width: 991px) {
            padding: 0 30px;
        }
        float: left;
        &.active{
            border: 2px solid #8a8dff;
            border-radius: 19px;
            margin: -2px;
            color: $black;
        }
    }
}
.delivery-select{
    &__item{
        background-color: #eef1f6;
        margin-bottom: 12px;
        border-radius: 30px;
        padding: 9px 30px 9px 74px;
        border: 2px solid #eef1f6;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-image: url('data:image/svg+xml,<svg fill="%23ccd4e1" viewBox="0 -46 417.81333 417" xmlns="http://www.w3.org/2000/svg"><path d="m159.988281 318.582031c-3.988281 4.011719-9.429687 6.25-15.082031 6.25s-11.09375-2.238281-15.082031-6.25l-120.449219-120.46875c-12.5-12.5-12.5-32.769531 0-45.246093l15.082031-15.085938c12.503907-12.5 32.75-12.5 45.25 0l75.199219 75.203125 203.199219-203.203125c12.503906-12.5 32.769531-12.5 45.25 0l15.082031 15.085938c12.5 12.5 12.5 32.765624 0 45.246093zm0 0"/></svg>');
        background-repeat: no-repeat;
        background-size: 16px;
        background-position: 30px center;
        &:last-child{
            margin-bottom: 0;
        }
        &.active{
            border-color: $success;
            background-image: url('data:image/svg+xml,<svg fill="%2384c76c" viewBox="0 -46 417.81333 417" xmlns="http://www.w3.org/2000/svg"><path d="m159.988281 318.582031c-3.988281 4.011719-9.429687 6.25-15.082031 6.25s-11.09375-2.238281-15.082031-6.25l-120.449219-120.46875c-12.5-12.5-12.5-32.769531 0-45.246093l15.082031-15.085938c12.503907-12.5 32.75-12.5 45.25 0l75.199219 75.203125 203.199219-203.203125c12.503906-12.5 32.769531-12.5 45.25 0l15.082031 15.085938c12.5 12.5 12.5 32.765624 0 45.246093zm0 0"/></svg>');
        }
        @media (max-width: 767px) {
            flex-wrap: wrap;
        }
    }
    &__logo{
        width: 70px;
        min-width: 70px;
        margin-right: 30px;
        text-align: center;
    }
    &__title{
        line-height: 16px;
        flex-grow: 1;
        font-weight: bold;
        @media (max-width: 767px) {
            flex: 1 0 100%;
            margin: 12px 0;
        }
        @media (min-width: 768px) and (max-width: 1229px) {
            flex: 1 0 290px;
        }
    }
    &__info{
        //font-size: 12px;
        color: #7a7a7a;
        margin-top: 6px;
        font-weight: normal;
    }
    &__time{
        flex-grow: 1;
        max-width: 210px;
    }
    &__price{
        font-weight: bold;
    }
}
.payment-select{
    .col-lg-10{
        @media (min-width: 1230px) {
            flex: 0 0 33.3333%;
            max-width: 33.3333%;
        }
    }
    &__item{
        cursor: pointer;
        border: 2px solid #eaedf2;
        border-radius: 19px;
        display: flex;
        margin-bottom: 22px;
        padding: 9px 20px;
        align-items: center;
        &.active {
            border: 2px solid #8a8dff;
        }
    }
    &__logo{
        width: 60px;
        min-width: 60px;
    }
    &__title{
        line-height: 16px;
        font-weight: bold;
    }
    &__info{
        //font-size: 12px;
        font-weight: normal;
        color: #7a7a7a;
        margin-top: 6px;
    }
}
