.brands{
    background-image: linear-gradient(to bottom, rgba(239,239,239,1) 0%,rgba(125,185,232,0) 100%);
    padding: 46px 0 0;
    position: relative;
    &:before {
        position: absolute;
        background-image: url("../img/bg-gray.png");
        background-repeat: repeat;
        background-color: #efefef;
        content: " ";
        right: 0;
        left: 0;
        display: block;
        top: 0;
        bottom: 0;
        z-index: -1;
    }
    &-list__item{
        height: 102px;
        line-height: 102px;
        text-align: center;
        position: relative;
        img{
            display: inline-block!important;
            width: unset!important;
        }
        a{
            filter: brightness(0) invert(85%);
            display: block;
            &:hover,
            &.active{
                filter: unset;
                &:before{
                    display: block;
                    @media (max-width: 991px) {
                        display: none;
                    }
                }
            }
            &:before{
                display: none;
                content: " ";
                width: 28px;
                height: 14px;
                position: absolute;
                bottom: 0;
                left: 50%;
                margin-left: -14px;
                border-bottom: 14px solid #f7f8fa;
                border-left: 14px solid transparent;
                border-right: 14px solid transparent;
            }
        }
    }
}
